*{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
}

body{
  padding: 60px;
  background-color: #000;
  color: #f5f5f5;
}

h2{
  display: inline-block !important;
  width: 67%;
}
h3{
  padding: 1rem;
}

form{
  display: inline-block;
}
input{
  width: 25rem;
  font-size: 1.3rem;
  color: #c5c5c5;
  margin-bottom: 1rem;
  border-radius: 7px;
  border: hidden;
  border: 1px solid #ffe3b3

}

input:hover{
  border: 1px solid #ffe3b3
}

.card{
  border-radius: 6px;
  margin: 0 10px 10px 0;
  overflow: hidden;
}

hr{
  margin-bottom: 1rem;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 150px;
  max-width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; 
}